.benefits {
    text-align: center;
    padding-bottom: 40px;
    line-height: 1.2;

    @media (min-width: $breakpoint-tablet) {
        padding-bottom: 80px;
    }

    &__items {
        margin-left: -15px;
        margin-right: -15px;
        display: flex;
        flex-flow: row wrap;
    }
    &__item {
        padding-bottom: 30px;
        padding-left: 15px;
        padding-right: 15px;
        width: 100%;

        @media (min-width: $breakpoint-tablet) {
            width: 50%;
        }
        @media (min-width: $breakpoint-desktop) {
            width: 33.33%;
        }
    }
    &__inner {
        padding: 40px 20px;
        height: 100%;
        background: linear-gradient(332.64deg, #e4e5ec -33.4%, #eeeff4 105.02%);
        box-shadow: -3px -4px 13px rgba(255, 255, 255, 0.7), 4px 4px 6px rgba(187, 191, 205, 0.7);
        border-radius: 12px;

        img {
            padding-bottom: 40px;
            max-width: 100px;

            @media (min-width: $breakpoint-desktop) {
                max-width: none;
            }
        }
    }
}
