.gallery {
    background-color: $color-white;
    margin: 0 -3000px;
    padding: 50px 3000px 70px;

    @media (min-width: $breakpoint-tablet) {
        padding-top: 100px;
        padding-bottom: 80px;
    }
    @media (min-width: $breakpoint-desktop) {
        padding-top: 140px;
        padding-bottom: 120px;
    }

    h2 {
        text-align: center;
    }

    .swiper {
        overflow: visible;
    }

    .swiper-horizontal > .swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
        bottom: auto;
        top: calc(100% + 20px);
    }

    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
    .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
        margin-left: 10px;
        margin-right: 10px;
    }

    .swiper-pagination-bullet {
        width: 14px;
        height: 14px;
        background: #eeeff4;
        box-shadow: inset 2px 1px 3px rgba(34, 5, 52, 0.33);
    }

    .swiper-pagination-bullet-active {
        background: linear-gradient(154.27deg, #07ceaa 18.35%, #088c6d 91.63%);
        box-shadow: -1px -1px 4px #ffffff, 1px 1px 4px rgba(0, 0, 0, 0.25);
    }

    .swiper-slide {
        width: 33.33%;
        text-align: center;

        img {
            max-width: 100%;
            border-radius: 12px;
        }
    }

    @media (min-width: $breakpoint-tablet) {
        .swiper-wrapper {
            flex-flow: row wrap;
        }
        .swiper-slide {
            padding: 0 10px 20px;
            width: 33.33%;

            @media (min-width: $breakpoint-desktop) {
                padding: 0 15px 30px;
            }

            &--half {
                width: 50%;
            }

            img {
                width: 100%;
                height: 200px;
                object-fit: cover;

                @media (min-width: $breakpoint-desktop) {
                    height: 340px;
                }
            }
        }
    }
}
