.header {
  padding: 0 1000px;
  margin-left: -1000px;
  margin-right: -1000px;

  &__inner {
    margin: auto;
    max-width: 1700px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    position: relative;
  }

  &__logo {
    margin-right: auto;
    max-width: 40%;
    @media (min-width: $breakpoint-mobile-extra) {
      max-width: 50%;
    }
    @media (min-width: $breakpoint-tablet) {
      max-width: none;
    }

    a {
      display: block;
    }
    img {
      max-width: 100%;
    }
  }

  &__menu {
    margin-left: -10px;
    margin-right: -10px;
    display: flex;
    flex-flow: row wrap;

    @media (min-width: $breakpoint-desktop-extra) {
      margin-left: -30px;
      margin-right: -30px;
    }

    a {
      padding: 15px 10px;
      font-weight: 600;
      line-height: 1;
      transition: color 0.2s ease-in-out;
      
      @media (min-width: $breakpoint-desktop-extra) {
        padding: 15px 30px;
      }

      &:hover {
        color: $color-lightblue;
      }
    }
  }

  &__city {
    margin-right: 30px;

    span {
      display: inline;
      position: relative;
      margin-right: 20px;
      border-bottom: 1px solid transparent;
      cursor: pointer;
      transition: border-color 0.2s ease-in-out;

      &:hover {
        border-bottom-color: $color-white;
      }

      &::after {
        content: '';
        margin-left: 10px;
        position: absolute;
        left: 100%;
        top: calc(50% - 3px);
        display: inline-block;
        width: 8px;
        height: 6px;
        background: url('../img/icon-down.svg') 50% 50% no-repeat;
      }
    }
  }

  &__support {
    margin-left: auto;
    color: $color-white-50a;

    @media (min-width: $breakpoint-desktop) {
      margin-left: 0;
      margin-right: auto;
    }

    a {
      margin-left: 5px;
      font-weight: 600;
    }
  }

  &__search {
    margin-left: -20px;
    margin-right: -20px;
    position: relative;
    z-index: 10;
    flex: 1 auto;
    order: 1;

    @media (min-width: $breakpoint-desktop) {
      margin-left: 0;
      margin-left: 30px;
      order: 0;
    }
  }

  &__btns {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
  }

  &__btn {
    margin-left: 15px;
    position: relative;
    display: inline-block;
    font-weight: 600;
    color: $color-font-dark;
    cursor: pointer;
    transition: color 0.2s ease-in-out;

    @media (min-width: $breakpoint-tablet) {
      margin-left: 30px;
    }

    &:hover {
      color: $color-lightblue;
    }

    img {
      padding-right: 10px;
      margin-top: -0.3em;
      display: inline-block;
      vertical-align: middle;
    }

    &--cart {
      ins {
        display: block;
        width: 22px;
        height: 22px;
        line-height: 22px;
        position: absolute;
        left: 12px;
        bottom: 70%;
        border-radius: 1000px;
        background-color: $color-lightblue;
        font-size: 12px;
        font-weight: 600;
        text-decoration: none;
        text-align: center;
        color: $color-white;
      }
    }
  }

  &__sandwich {
    position: relative;
    display: inline-block;
    padding-left: 30px;
    cursor: pointer;

    ins {
      display: block;
      border-radius: 1000px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transform-origin: 50% 50%;
      height: 2px;
      width: 20px;
      background-color: $color-font-dark;
      transition-property: transform, opacity;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out;

      &::before,
      &::after {
        content: '';
        border-radius: 1000px;
        position: absolute;
        left: 0;
        top: 0;
        height: 2px;
        width: 20px;
        background-color: $color-font-dark;
        transform-origin: 50% 50%;
        transition-property: transform, opacity;
        transition-duration: 0.3s;
        transition-timing-function: ease-in-out;
      }
      &::before {
        transform: translateY(-6px);
      }
      &::after {
        transform: translateY(6px);
      }
    }

    &.active {
      ins {
        transform: translateY(-50%) rotate(-45deg);

        &::before {
          transform: translateY(0) rotate(-90deg);
        }
        &::after {
          transform: translateY(0);
          opacity: 0;
        }
      }
    }
  }

  &__dropdown {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    z-index: 10;
    background-color: $color-white;
    opacity: 0;
    pointer-events: none;
    transition-property: opacity, top;
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -1000px;
      right: -1000px;
      bottom: 0;
      background-color: $color-white;
      z-index: -1;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -1000px;
      right: -1000px;
      bottom: -10000000px;
      background-color: $color-blue-a;
      z-index: -2;
    }

    &-inner {
      margin: auto;
      padding-top: 40px;
      max-width: 1700px;
      width: 100%;
      border-top: 1px solid $color-lightgray2;
      display: flex;
      flex-flow: row wrap;
    }

    &-item {
      padding-bottom: 40px;
      width: 50%;
      display: flex;
      flex-flow: row nowrap;

      @media (min-width: $breakpoint-desktop-extra) {
        width: 33.33%;
      }

      ul {
        display: flex;
        flex-flow: row wrap;
      }

      li {
        &::after {
          content: '/';
          padding-left: 10px;
          padding-right: 10px;
          display: inline-block;
          color: $color-lightgray2;
        }

        &:last-child::after {
          display: none;
        }
      }

      a {
        font-size: 16px;
        color: $color-font;
        transition: color 0.2s ease-in-out;

        &:hover {
          color: $color-lightblue;
        }
      }
    }
    &-pic {
      width: 100px;

      img {
        border-radius: 5px;
        border: 1px solid $color-lightgray2;
        max-width: 100%;
      }
    }
    &-content {
      padding-left: 20px;
      width: calc(100% - 100px);

      & > a {
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: 600;
        color: $color-font;
        transition: color 0.2s ease-in-out;

        &:hover {
          color: $color-lightblue;
        }
      }
    }
    // mod
    &.active {
      opacity: 1;
      top: 100%;
      pointer-events: all;
    }
  }

  // mod
  &--line {
    margin-bottom: 20px;
    padding-top: 0.3em;
    padding-bottom: 0.3em;
    background-color: $color-blue;
    font-size: 14px;
    color: $color-white;

    @media (min-width: $breakpoint-tablet) {
      font-size: 16px;
    }
    @media (min-width: $breakpoint-desktop) {
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }

    a {
      color: $color-white;
    }
  }
}
