.map {
    position: relative;

    &__map {
        height: 600px;
    }

    &__inner {
        padding-left: 20px;
        padding-right: 20px;
        position: absolute;
        z-index: 1;
        top: -30px;
        left: 50%;
        width: 100%;
        max-width: 960px;
        transform: translateX(-50%);
    }

    &__content {
        padding: 30px;
        background-color: $color-green;
        width: 100%;
        max-width: 400px;
        border-radius: 12px;
        line-height: 1.2;
        font-size: 20px;
        font-weight: lighter;
        color: $color-white;

        @media (min-width: $breakpoint-tablet) {
            padding: 50px 70px;
        }

        img {
            margin-bottom: 20px;
        }
    }

}