.reviews {
    margin: 0 -3000px;
    padding: 40px 3000px;
    background: linear-gradient(180deg, #daddea 0%, #eeeff4 110.53%);

    @media (min-width: $breakpoint-desktop) {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    h2 {
        text-align: center;
    }

    p {
        margin-bottom: 1em;

        &:last-child {
            margin-bottom: 0;
        }
    }

    a {
        &:link,
        &:visited {
            border-bottom: 1px solid $color-green;
            transition-property: color, border-color;
            transition-duration: 0.2s;
            transition-timing-function: ease-in-out;
        }

        &:hover {
            border-bottom-color: transparent;
        }
    }

    &__add {
        padding-top: 30px;
        text-align: center;
    }

    &__contact {
        padding-bottom: 20px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;

        &-pic {
            width: 60px;

            @media (min-width: $breakpoint-desktop) {
                width: 110px;
            }

            img {
                max-width: 100%;
                border-radius: 1000px;
            }
        }
        &-data {
            width: calc(100% - 60px);
            padding-left: 20px;

            @media (min-width: $breakpoint-desktop) {
                width: calc(100% - 110px);
                padding-left: 60px;
            }
        }

        &-date {
            padding-bottom: 10px;
            color: $color-lightgray2;
        }

        &-name {
            padding-bottom: 5px;
            font-weight: 600;
            line-height: 1;
            font-size: 22px;
        }

        &-score {
            color: $color-red;
        }
    }
}

.swiper-reviews {
    overflow: visible;
    max-width: 730px;
    margin: auto;
    padding-bottom: 70px;

    @media (min-width: $breakpoint-desktop) {
        padding-bottom: 0;
    }

    .swiper-slide {
        padding: 30px;
        pointer-events: none;
        background: linear-gradient(153.11deg, #f1f2f6 -6.51%, #ffffff 121.86%);
        box-shadow: -2px -4px 7px rgba(251, 252, 255, 0.7), 4px 4px 5px rgba(187, 191, 205, 0.7);
        border-radius: 12px;
        transition-property: opacity;
        transition-duration: 0.2s;
        transition-timing-function: ease-in-out;

        @media (min-width: $breakpoint-desktop) {
            opacity: 0;
            padding: 40px 90px;
        }

        &-active {
            pointer-events: all;

            @media (min-width: $breakpoint-desktop) {
                opacity: 1;
            }
        }
    }

    .swiper-button-next,
    .swiper-button-prev {
        margin-top: 0;
        width: 45px;
        height: 45px;
        top: auto;
        bottom: 0;
        box-shadow: -2px -4px 7px #ffffff, 4px 4px 5px rgba(187, 191, 205, 0.1);
        border-radius: 1000px;
        transition: box-shadow 0.2s ease-in-out;

        @media (min-width: $breakpoint-desktop) {
            margin-top: -32px;
            width: 65px;
            height: 65px;
            top: 50%;
            bottom: auto;
        }

        &:hover {
            box-shadow: -2px -4px 7px #ffffff, 4px 4px 5px rgba(187, 191, 205, 0.7);
        }
        &::after {
            display: none;
        }
    }
    .swiper-button-next {
        right: 50%;
        margin-right: -50px;
        background: url('../img/next.svg') 60% 50% no-repeat,
            linear-gradient(153.11deg, #f1f2f6 -6.51%, #ffffff 121.86%);
        background-size: 20px auto;

        @media (min-width: $breakpoint-desktop) {
            margin-right: 0;
            right: -100px;
            left: auto;
            background-size: auto;
        }

        &.swiper-button-disabled {
            opacity: 1;
            background: url('../img/next-disabled.svg') 50% 50% no-repeat,
                linear-gradient(153.11deg, #f1f2f6 -6.51%, #ffffff 121.86%);
        }
    }
    .swiper-button-prev {
        left: 50%;
        margin-left: -50px;
        background: url('../img/prev.svg') 40% 50% no-repeat,
            linear-gradient(153.11deg, #f1f2f6 -6.51%, #ffffff 121.86%);
        background-size: 20px auto;

        @media (min-width: $breakpoint-desktop) {
            margin-left: 0;
            left: -100px;
            right: auto;
            background-size: auto;
        }

        &.swiper-button-disabled {
            opacity: 1;
            background: url('../img/prev-disabled.svg') 50% 50% no-repeat,
                linear-gradient(153.11deg, #f1f2f6 -6.51%, #ffffff 121.86%);
        }
    }
}
