.promo {
    margin-bottom: 50px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 18px;

    @media (min-width: $breakpoint-tablet) {
        height: 715px;
        margin-bottom: 100px;
    }
    @media (min-width: $breakpoint-desktop) {
        margin-bottom: 150px;
    }

    h1 {
        margin-bottom: 0.7em;
        font-family: $font-family-russo;
        font-size: 24px;
        line-height: 1.1;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: $color-red;
        text-shadow: -1px -1px 6px #ffffff, -1px -1px 6px #ffffff, 1px 1px 4px rgba(58, 72, 109, 0.7);

        @media (min-width: $breakpoint-tablet) {
            margin-bottom: 30px;
            font-size: 52px;
        }
    }
    &__bg {
        margin-left: -20px;
        margin-right: -20px;
        margin-bottom: 30px;
        width: calc(100% + 40px);
        padding-bottom: 70%;
        background: url('../img/head-bg.svg') 48% 36% no-repeat;
        background-size: 350% auto;

        @media (min-width: $breakpoint-tablet) {
            margin-bottom: 0;
            margin-left: 0;
            margin-right: 0;
            padding-bottom: 0;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: url('../img/head-bg.svg') calc(100% + 760px) calc(50% - 40px) no-repeat;
        }
        @media (min-width: $breakpoint-desktop) {
            background: url('../img/head-bg.svg') calc(100% + 390px) calc(50% - 40px) no-repeat,
                url('../img/head-bg-left.svg') 0 100% no-repeat;
        }
    }
    &__inner {
        position: relative;
        z-index: 1;
        width: 100%;
        max-width: 1110px;
    }
    &__content {
        max-width: 500px;

        p {
            max-width: 400px;
        }
    }
    &__btn {
        padding-top: 20px;

        @media (min-width: $breakpoint-tablet) {
            padding-top: 40px;
        }

        a {
            position: relative;
            display: inline-block;

            &::before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(136.01deg, #ff6715 -16.14%, #ff222e 101.49%);
                box-shadow: -3px -3px 7px #ffffff, 3px 3px 4px rgba(94, 100, 117, 0.5);
                border-radius: 6px;
                z-index: -1;
                transition: opacity 0.2s ease-in-out;
            }
            &::after {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(0deg, #ff222e, #ff222e);
                border-radius: 6px;
                z-index: -1;
                opacity: 0;
                transition: opacity 0.2s ease-in-out;
            }
            &:link,
            &:visited {
                padding: 10px 30px;
                font-weight: 600;
                font-size: 18px;
                color: $color-white;
            }
            &:hover {
                &::before {
                }
                &::after {
                    opacity: 1;
                }
            }
        }
    }
}
